<template>
	<div>
		<ul class="cart__orders">
			<li class="cart__order" v-for="product in products" :key="product.product.id">
				<h3>{{ product.product.title }}</h3>
				<b> {{ product.product.price * product.amount | numberFormat}} ₽</b>
				<span>Артикул: {{ product.product.id }}</span>
			</li>
		</ul>

		<div class="cart__total">
			<p>Доставка: <b>500 ₽</b></p>
			<p>Итого: <b>{{ amount}}</b> товара на сумму <b>{{ totalPrice | numberFormat }} ₽</b></p>
		</div>
	</div>
</template>

<script>
  import numberFormat from '@/helpers/numberFormat';

	export default {
		props: ['products', 'amount', 'totalPrice'],

		filters: {
			numberFormat
		}
	}
</script>