import Vue from 'vue';
import Vuex from 'vuex';

import axios from 'axios';
import { API_URLS } from '@/config';

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
		cartProducts: [],
		cartProductsData: [],
		cartLoader: false,

		userAccessKey: null,

		orderInfo: {
			basket: {
				items: []
			}
		},
	},

	mutations: {
		updateOrderInfo(state, orderInfo) {
			state.orderInfo = orderInfo;

			if (state.orderInfo) {
				state.orderInfo.basket.items = state.orderInfo.basket.items.map(item => {
					let value = {
						...item, 
						amount: item.quantity
					};
					delete value.quantity;

					return value;
				})
			}
		},

		resetCart(state) {
			state.cartProducts = [];
			state.cartProductsData = [];
		},

		cartLoaderStart(state) {
		 	state.cartLoader = true;
		},

		cartLoaderStop(state) {
			setTimeout(() => {state.cartLoader = false}, 1000);
		},

		updateCartProductAmount(state, {productId, amount}) {
			state.cartLoader = true;

			const item = state.cartProducts.find(item => item.productId === productId);

			if (item) {
				item.amount = amount;
			}

			setTimeout(() => {state.cartLoader = false}, 1000);
		},

		deleteCartProduct(state, productId) {
			state.cartLoader = true;

			state.cartProducts = state.cartProducts.filter(item => item.productId !== productId);

			setTimeout(() => {state.cartLoader = false}, 1000);
		},

		updateUserAccessKey(state, accessKey) {
			state.userAccessKey = accessKey;
		},

		updateCartProductsData(state, items) {
			state.cartProductsData = items;
		},

		syncCartProducts(state) {
			state.cartProducts = state.cartProductsData.map(item => {
				return {
					productId: item.product.id,
					amount: item.quantity
				};
			})
		}

	},

	getters: {
		cartDetailProducts(state) {
			return state.cartProducts.map(item => {
				const product = state.cartProductsData.find(p => p.product.id === item.productId).product;

				return {
					...item,
					product: {
						...product,
						image: product.image.file.url
					}
				};
			});
		},

		getTotalPrice(state, getters) {
			return getters.cartDetailProducts.reduce((acc, item) => item.product.price * item.amount + acc, 0);
		},
	},

	actions: {
		async loadOrderInfo(context, orderId) {
			context.state.cartLoader = true;

			return axios
				.get(API_URLS.order + '/' + orderId, {
					params: {
						userAccessKey: context.state.userAccessKey
					}
				})
				.then(response => context.commit('updateOrderInfo', response.data))
				.then(() => context.state.cartLoader = false)
		},

		async loadCart(context) {
			context.commit('cartLoaderStart');

			return axios
				.get(API_URLS.cart, {
					params: {
						userAccessKey: context.state.userAccessKey
					}
				})
				.then((response) => {
					if (!context.state.userAccessKey) {
						localStorage.setItem('userAccessKey', response.data.user.accessKey);		
						context.commit('updateUserAccessKey', response.data.user.accessKey);
					}

					context.commit('updateCartProductsData', response.data.items);
					context.commit('syncCartProducts');
					context.commit('cartLoaderStop')
				});
		},

		async addProductToCart(context, {productId, amount}) {
			context.commit('cartLoaderStart');

			return (new Promise(resolve => setTimeout(resolve, 1000)))
				.then(() => {
					return axios.post(API_URLS.productToCart, {
						productId: productId, 
						quantity: amount
					}, {
						params: {
							userAccessKey: context.state.userAccessKey
						}
					})
					.then(response => {
						context.commit('updateCartProductsData', response.data.items);
						context.commit('syncCartProducts');
						context.commit('cartLoaderStop')
					})
				})
		},

		async updateCartProductAmount(context, {productId, amount}) {
			context.commit('updateCartProductAmount', {productId, amount});

			if (amount < 1) {
				return;
			}
			
			return axios.put(API_URLS.productToCart, {
				productId: productId, 
				quantity: amount
			}, {
				params: {
					userAccessKey: context.state.userAccessKey
				}
			})
			.then(response => {
				context.commit('updateCartProductsData', response.data.items);
			})
			.catch(() => {
				context.commit('syncCartProducts');
			});
		},

		async deleteProductFromCart(context, productId) {
			context.commit('cartLoaderStart');

			return (new Promise(resolve => setTimeout(resolve, 1000)))
				.then(() => {
					return axios.delete(API_URLS.deleteFromCart, {
						data: {
							productId: productId
						},
						params: {
							userAccessKey: context.state.userAccessKey
						}
					})
					.then(response => {
						context.commit('updateCartProductsData', response.data.items);
						context.commit('syncCartProducts');
						context.commit('cartLoaderStop')
					})
				})
		},
	}
})