<template>
  <main class="content container">
    <div class="content__top content__top--catalog">
      <h1 class="content__title">
        Каталог
      </h1>
      <span class="content__info">
        152 товара
      </span>
    </div>

    <div class="content__catalog">
      <ProductFilter :page.sync="page" :filter.sync="filter"/>

      <section class="catalog">
				<BaseLoader v-if="loading.inProcess" />
				<div v-else-if="loading.failed" class="message message--error">
					<p>При загрузке товаров произошла ошибка.</p>
					<button @click.prevent="loadProducts">Попробовать еще раз</button>
				</div>
				<div v-else>
					<ProductList :products="products"/>
					<BasePagination v-model="page" :count="countProducts" :per-page="productsPerPage"/>
				</div>
      </section>
    </div>

  </main>

</template>

<script>
	import ProductList from '@/components/ProductList.vue';
	import BasePagination from '@/components/BasePagination.vue';
	import ProductFilter from '@/components/ProductFilter.vue';
	import BaseLoader from '@/components/BaseLoader.vue';

	import axios from 'axios';
	import { API_URLS } from '@/config';

	export default {
		components: {ProductList, BasePagination, ProductFilter, BaseLoader},

		data() {
			return {
				filter: {
					priceFrom: 0,
					priceTo: 0,
					categoryId: 0,
					color: {},
				},

				page: 1,
				productsPerPage: 6,
				productsData: null,

				loading: {
					inProcess: false,
					failed: false,
				},
			};
		},

		computed: {
			products() {
				return this.productsData ? 
				this.productsData.items.map(product => {
					return {
						...product,
						image: product.image.file.url
					}
				}) : 
				[];
			},

			countProducts() {
				return this.productsData ? this.productsData.pagination.total : 0;
			}
		},

		methods: {
			// загрузка товаров.
			loadProducts() {
				this.loading.inProcess = true;
				this.loading.failed = false;

				axios
					.get(API_URLS.products, {
						params: {
							page: this.page,
							limit: this.productsPerPage,
							categoryId: this.filter.categoryId,
							minPrice: this.filter.priceFrom,
							maxPrice: this.filter.priceTo,
							colorId: this.filter.color.id
						}
					})
					.then(response => this.productsData = response.data)
					.catch(() => this.loading.failed = true)
					.then(() => this.loading.inProcess = false);
			},
		},

		watch: {
			page() {
				this.loadProducts();
			},

			filter() {
				this.loadProducts();
			}
		},

		created() {
			this.loadProducts();
		}
	}
</script>


