<template>

	<main class="content container">
		<BaseLoader v-if="cartUpdating" />

		<div class="content__top">
			<ul class="breadcrumbs">
				<li class="breadcrumbs__item">
					<router-link class="breadcrumbs__link" :to="{name: 'main'}">
						Каталог
					</router-link>
				</li>
				<li class="breadcrumbs__item">
					<a class="breadcrumbs__link">
						Корзина
					</a>
				</li>
			</ul>

			<h1 class="content__title">
				Корзина
				<button @click.prevent="refreshCart">Обновить</button>
			</h1>
			<span class="content__info">
				товаров: {{ products.length }}
			</span>
		</div>

		<section class="cart">
			<form class="cart__form form" action="#" method="POST">
				<div class="cart__field">
					<ul class="cart__list">
						<CartItem v-for="item in products" :key="item.productId" :item="item" />
					</ul>
				</div>

				<div class="cart__block">
					<p class="cart__desc">
						Мы&nbsp;посчитаем стоимость доставки на&nbsp;следующем этапе
					</p>
					<p class="cart__price">
						Итого: <span> {{ totalPrice | numberFormat}} ₽</span>
					</p>

					<router-link :to="{name: 'order'}" tag="button" class="cart__button button button--primery" type="submit" v-if="$store.state.cartProducts.length">
						Оформить заказ
					</router-link>
				</div>
			</form>
		</section>
	</main>
</template>

<script>
	import numberFormat from '@/helpers/numberFormat';
	import { mapGetters } from 'vuex';
	import CartItem from '@/components/CartItem';
	import BaseLoader from '@/components/BaseLoader.vue';

  export default {
		filters: {
			numberFormat,
		},

		computed: {
			...mapGetters({products: 'cartDetailProducts', totalPrice: 'getTotalPrice'}),
			
			cartUpdating() {
				return this.$store.state.cartLoader;
			},

		},

		methods: {
			refreshCart() {
				this.$store.dispatch('loadCart');
			},
		},

		components: {CartItem, BaseLoader},
  }
</script>

<style>
  .content__title {
		display: flex;
		align-items: center;
	}
	.content__title button {
		margin-left: 20px;
	}
</style>