<template>
	<div>
		<li class="cart__item product" >
			<div class="product__pic">
				<img :src="item.product.image" width="120" height="120" :alt="item.product.title">
			</div>
			<h3 class="product__title">
				{{ item.product.title }}
			</h3>
			<span class="product__code">
				Артикул: {{ item.productId }}
			</span>

			<ProductAmount :product-amount.sync="amount" :classes="classes" />

			<b class="product__price">
				{{ (item.amount * item.product.price) | numberFormat}} ₽
			</b>

			<button class="product__del button-del" type="button" aria-label="Удалить товар из корзины" @click.prevent="delFromCart(item.productId)">
				<svg width="20" height="20" fill="currentColor">
					<use xlink:href="#icon-close"></use>
				</svg>
			</button>
		</li>

		<BaseLoader v-if="deleteInProcess" />
	</div>
</template>


<script>
	import ProductAmount from '@/components/ProductAmount.vue';
	import BaseLoader from '@/components/BaseLoader.vue';
	import numberFormat from '@/helpers/numberFormat.js';

//	import { mapMutations, mapActions } from 'vuex';
	import { mapActions } from 'vuex';

  export default {
		props: ['item'],

		data () {
			return {
				classes: ['product__counter', 'form__counter'],
				deleteInProcess: false,
			}
		},
		
 		filters: {
			numberFormat,
		},

		computed: {
			amount: {
				get() {
					return this.item.amount;
				},
				set(value) {
					this.$store.dispatch('updateCartProductAmount', {productId: this.item.productId, amount: value});
				}
			}
		},

		methods: {
			...mapActions(['deleteProductFromCart']),

			delFromCart(productId) {
				this.deleteInProcess = true;

				this.deleteProductFromCart(productId)
				.then(() => {
					this.deleteInProcess = false;
				});
			},
		},

    components: {ProductAmount, BaseLoader},
}
</script>