<template>
	<div :class="classes">
		<button type="button" aria-label="Убрать один товар" @click.prevent="decrement">
			<svg width="12" height="12" fill="currentColor">
				<use xlink:href="#icon-minus"></use>
			</svg>
		</button>

		<input type="text" name="count" v-model.number="amount">

		<button type="button" aria-label="Добавить один товар" @click.prevent="increment">
			<svg width="12" height="12" fill="currentColor">
				<use xlink:href="#icon-plus"></use>
			</svg>
		</button>
	</div>
</template>

<script>
	export default {
		data() {
			return {

			};
		},

		props: ['productAmount', 'classes'],

		computed: {
			amount: {
				get() {
					return this.productAmount;
				},

				set (value) {
					this.$emit('update:productAmount', value);
				}
			}
		},

		methods: {
			increment() {
				this.amount = this.amount >= 10 ? this.amount : this.amount + 1;
			},

			decrement() {
				this.amount = this.amount <= 1 ? 1 : this.amount - 1;
			}
		},
	}
</script>

<style>
  button {
		cursor: pointer;
	}
</style>