<template>
	<ul class="colors colors--black">
		<li class="colors__item" v-for="color in colors" :key="color.id">
			<label class="colors__label">
				<input class="colors__radio sr-only" type="radio" :value="color" v-model="colorSelect" >
				<span class="colors__value" :style="{backgroundColor: color.code, boxShadow: '0px 0px 30px 5px grey'}">
				</span>
			</label>
		</li>
	</ul>
</template>

<script>
	export default {
	  props: ['colors', 'colorSelected'],
		computed: {
			colorSelect: {
				get() {
					return this.colorSelected;
				},
				set(value) {  
					this.$emit('update:colorSelected', value);
				},
			},
		},
	}
</script>

