<template>
  <main class="content container">
    <div class="content__top">
      <ul class="breadcrumbs">
        <li class="breadcrumbs__item">
					<router-link class="breadcrumbs__link" :to="{name: 'main'}">
						Каталог
					</router-link>
        </li>
        <li class="breadcrumbs__item">
					<router-link class="breadcrumbs__link" :to="{name: 'cart'}">
						Корзина
					</router-link>
        </li>
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link">
            Оформление заказа
          </a>
        </li>
      </ul>

      <h1 class="content__title">
        Корзина
      </h1>
      <span class="content__info">
        Товаров в корзине: {{ $store.state.cartProducts.length }}
      </span>
    </div>

    <section class="cart">
      <form class="cart__form form" action="#" method="POST" @submit.prevent="order">
        <div class="cart__field">
          <div class="cart__data">
						<BaseFormText :title="formTitles.name" :error="formError.name" placeholder="Введите ваше полное имя" v-model="formData.name"/>
						<BaseFormText :title="formTitles.address" :error="formError.address" placeholder="Введите ваш адрес" v-model="formData.address"/>
						<BaseFormText :title="formTitles.phone" :error="formError.phone" placeholder="Введите ваш телефон" v-model="formData.phone"/>
						<BaseFormText :title="formTitles.email" :error="formError.email" placeholder="Введите ваш Email" v-model="formData.email"/>
						<BaseFormTextarea :title="formTitles.comment" :error="formError.comment" placeholder="Введите ваше полное имя" v-model="formData.comment"/>
					</div>

          <div class="cart__options">
            <h3 class="cart__title">Доставка</h3>
            <ul class="cart__options options">
              <li class="options__item">
                <label class="options__label">
                  <input class="options__radio sr-only" type="radio" name="delivery" value="0" checked="">
                  <span class="options__value">
                    Самовывоз <b>бесплатно</b>
                  </span>
                </label>
              </li>
              <li class="options__item">
                <label class="options__label">
                  <input class="options__radio sr-only" type="radio" name="delivery" value="500">
                  <span class="options__value">
                    Курьером <b>500 ₽</b>
                  </span>
                </label>
              </li>
            </ul>

            <h3 class="cart__title">Оплата</h3>
            <ul class="cart__options options">
              <li class="options__item">
                <label class="options__label">
                  <input class="options__radio sr-only" type="radio" name="pay" value="card">
                  <span class="options__value">
                    Картой при получении
                  </span>
                </label>
              </li>
              <li class="options__item">
                <label class="options__label">
                  <input class="options__radio sr-only" type="radio" name="pay" value="cash">
                  <span class="options__value">
                    Наличными при получении
                  </span>
                </label>
              </li>
            </ul>
          </div>
        </div>

        <div class="cart__block">
          <OrderCart :products="$store.getters.cartDetailProducts" :amount="$store.state.cartProducts.length" :totalPrice="$store.getters.getTotalPrice" />

          <button class="cart__button button button--primery" type="submit">
            Оформить заказ
          </button>
        </div>
        <div class="cart__error form__error-block" v-if="formErrorMessage">
          <h4>Заявка не отправлена!</h4>
          <p>
            {{ formErrorMessage }}
          </p>
        </div>
      </form>
    </section>

		<BaseLoader v-if="orderSending" />
  </main>
</template>

<script>
	import BaseLoader from '@/components/BaseLoader.vue';
	import BaseFormText from '@/components/BaseFormText.vue';
	import BaseFormTextarea from '@/components/BaseFormTextarea.vue';
  import OrderCart from '@/components/OrderCart.vue';

	import axios from 'axios';
  import { API_URLS } from '@/config.js';

  import numberFormat from '@/helpers/numberFormat';

	export default {
		components: {BaseFormText, BaseFormTextarea, BaseLoader, OrderCart},

		data() {
      return {
				/* lable для input*/
				formTitles: {
					name: 'ФИО',
					comment: 'Комментарий к заказу',
					address: 'Адрес доставки',
					phone: 'Телефон',
					email: 'Email',
				},
				/* данные для отправки */
				formData: {},
				/* сообщения об ошибках в input */
				formError: {},
				/* сообщения об ошибке при заказе товара */
        formErrorMessage: '',
        orderSending: false,
			};
    },

    filters: {
      numberFormat,
    },

    // computed: {
    //   cartDetailProducts() {
    //     return this.$store.getters.cartDetailProducts;
    //   },

    //   cartProductsCount() {
    //     return this.$store.state.cartProducts.length;
    //   },
       
    //   cartTotalPrice() {
    //     return this.$store.getters.getTotalPrice;
    //   },
    // },

		methods: {
			order() {
        this.formError = {};
        this.formErrorMessage = '';
        this.orderSending = true;

        setTimeout(async ()=> {
				  return axios.post(API_URLS.order,
					this.formData,
					{
						params: {
							userAccessKey: this.$store.state.userAccessKey
						}
					})
        .then((response) => {
          this.$store.commit('resetCart');
          this.$store.commit('updateOrderInfo', response.data);
          this.$router.push({name: 'orderInfo', params: {id: response.data.id}});
        })
        .catch(error => {
          this.formError = error.response.data.error.request || {};
          this.formErrorMessage = error.response.data.error.message;
        })
        .then(() => this.orderSending = false)
        },
        1000);
			}
		}
	}
</script>