<template>
	<router-link class="header__cart" aria-label="Корзина с товарами" :to="{name: 'cart'}">
		<svg width="30" height="21" fill="currentColor">
			<use xlink:href="#icon-cart"></use>
		</svg>
		<span class="header__count" :class="cartClasses" aria-label="Количество товаров">{{ $store.state.cartProducts.length }}</span>
	</router-link>
</template>  

<script>
  export default {
		data() {
			return {

			}
		},

		computed: {
			cartUpdating() {
				return this.$store.state.cartLoader;
			},

			cartClasses() {
				return this.cartUpdating ? ['cart-rotating'] : [''];
			},
		},
	}
</script>

<style>
  .cart-rotating {
		animation: amount-rotate 1s infinite;
	}

	@keyframes amount-rotate{
		0% {transform: rotate(0deg)}
		100% {transform: rotate(360deg)}
	}

</style>